"use client";
import React, { useEffect, useRef } from "react";
import ThreeDModel from "@/components/ThreeDModel";

export default function SacredMatrixPage() {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    // Set canvas size
    const resizeCanvas = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };
    resizeCanvas();
    window.addEventListener("resize", resizeCanvas);

    // Matrix characters (symbols of God)
    const chars = "✝☦✚✛✜✝✞✟✠✡☥☦☧☨☩☪☫☬☭☮☯☸☦✝✞✟✠✡";
    const fontSize = 14;
    const columns = Math.floor(canvas.width / fontSize);
    const drops: number[] = [];

    for (let i = 0; i < columns; i++) {
      drops[i] = 1;
    }

    const draw = () => {
      ctx.fillStyle = "rgba(0, 0, 0, 0.05)";
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      ctx.fillStyle = "#00ffff";
      ctx.font = fontSize + "px monospace";

      for (let i = 0; i < drops.length; i++) {
        const text = chars[Math.floor(Math.random() * chars.length)];
        ctx.fillText(text, i * fontSize, drops[i] * fontSize);

        if (drops[i] * fontSize > canvas.height && Math.random() > 0.975) {
          drops[i] = 0;
        }
        drops[i]++;
      }
    };

    const interval = setInterval(draw, 33);

    return () => {
      clearInterval(interval);
      window.removeEventListener("resize", resizeCanvas);
    };
  }, []);

  return (
    <div className="min-h-screen bg-black text-cyan-400 relative overflow-hidden">
      {/* Matrix Background */}
      <canvas
        ref={canvasRef}
        className="absolute inset-0 w-full h-full z-0"
        style={{ opacity: 0.3 }}
      />
      
      {/* Content */}
      <div className="relative z-10">
        <div className="container mx-auto px-4 py-16">
          <div className="text-center mb-12">
            <h1 className="text-6xl md:text-8xl font-bold mb-6" style={{
              fontFamily: "Orbitron, monospace",
              background: "linear-gradient(45deg, #00ffff, #0066ff)",
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
              textShadow: "0 0 30px rgba(0,255,255,0.8)"
            }}>
              SACRED MATRIX
            </h1>
            <p className="text-2xl text-cyan-300 mb-8">
              The Divine Cross in the Digital Realm
            </p>
          </div>

          <div className="flex flex-col items-center justify-center">
            <div className="w-full max-w-2xl h-96 mb-8">
              <ThreeDModel />
            </div>
            
            <div className="text-center max-w-2xl">
              <p className="text-xl text-cyan-300 mb-6">
                Behold the sacred symbol of TriCoin, rotating in the divine matrix of digital consciousness.
                Each rotation represents the eternal cycle of creation and destruction.
              </p>
              
              <div className="grid md:grid-cols-3 gap-6 mt-8">
                <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-4">
                  <h3 className="text-lg font-bold text-cyan-300 mb-2">Divine Symbol</h3>
                  <p className="text-cyan-400 text-sm">The cross represents the intersection of technology and spirituality</p>
                </div>
                <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-4">
                  <h3 className="text-lg font-bold text-cyan-300 mb-2">Sacred Geometry</h3>
                  <p className="text-cyan-400 text-sm">Built on the golden ratio φ = 1.61803398875</p>
                </div>
                <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-4">
                  <h3 className="text-lg font-bold text-cyan-300 mb-2">Eternal Motion</h3>
                  <p className="text-cyan-400 text-sm">Continuous rotation symbolizes the eternal nature of divine truth</p>
                </div>
              </div>

              <div className="mt-12">
                <a href="/" className="px-8 py-4 bg-gradient-to-r from-blue-600 to-cyan-500 text-black rounded-lg font-bold hover:from-cyan-500 hover:to-blue-600 transition-all duration-300">
                  Return to TriCoin
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Overlay for better text visibility */}
      <div className="absolute inset-0 bg-black/50 z-5"></div>
    </div>
  );
}
